html, body{
  position: absolute;
    min-width: 100%;
    min-height: 100%;
    margin:0;
    padding:0;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.dashboard{
  width:100%;
  height: inherit;
  background-color: rgb(210, 208, 208);
  padding-bottom: 5px;
}

.dashboard .topbar {
  z-index: 999;
  display: flex;
  flex-direction: row;
  position: fixed;
  background-color:  rgb(42, 42, 165);
  width: 100%;
  padding: 7px;
  height: 66px;
}
.dashboard .content{
  display: flex;
  flex-direction: row;
  align-items: center;
  }
.dashboard .content img{
  width: auto; 
  height: 70px; 
  display: block; 
  border-radius: 10px;
  margin-top: -10px;
}
.dashboard .content .name {
  padding-top: 10px;
  color: white;
  padding-left: 0px;
  font-size: 45px;
  white-space: wrap;
  overflow: hidden;
  transition: font-size 0.5s ease; 
}
@media(max-width: 700px)
{
  .dashboard .content{
    width:500px;
  }
  .dashboard .content .name {
    font-size: 35px; 
    margin-right: 40px;
    text-align: center;
  }
}
@media(max-width: 590px)
{
  .dashboard .content{
    width:400px;
  }
  .dashboard .content .name {
    padding-top: 20px;
  }
}

.dashboard .topbar .profile{
  position: fixed;
  top:5px;
  right:0px;
  margin-right: 1%;
  display: inline-block;
}
.dashboard .profile img{
  height: 35px;
  width: auto;
  border-radius: 50%;
  align-self: center;
}
.dashboard .profile button {
  background-color: rgb(157, 222, 248);
  text-align: center;
  padding: 4px 5px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.dashboard .profile button:hover{
  background-color:  #ddd;
}
.dashboard .dropdown-content{
  display: none; /* Initially hide the dropdown content */
  position: absolute;
  right: 0%;
  top: 100%;
  background-color: white;
  min-width: 100px;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 10%;
  z-index: 2;
}
.dashboard .dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dashboard .dropdown-content a:hover {
  background-color: #ddd;
}
.dashboard .show {
  display: block; /* Display the dropdown content when the "show" class is added */
}
.dashboard .sidebar .locationSettings .dropdown-contentB.flex {
  display: flex; /* Display the dropdown content when the "show" class is added */

}

.dashboard .sidebar .locationSettings .dropdown-contentB.hidden {
  display: none; /* Display the dropdown content when the "show" class is added */
}

/* going into dangerous territory */
.dashboard .lower-content{
  flex-direction: row;
  /* height: calc(100vh - 90px);
  padding-top: 90px;padding-top: 103px; */
  height: inherit;
  min-height: 500px;
  padding-top: 86px;
  display: flex;
  width: 100%;
  gap: 5px;
}
.dashboard .lower-content .saved .sidebar-container{
  position: sticky;
  max-width: 30vw;
  min-width: 450px;
  height: inherit;
  display: block;
  border-radius: 10px;
  flex: 1;
  margin-right: 5px;
}
.dashboard .lower-content .saved .sidebar{
  position: absolute;
  max-width: 30vw;
  min-width: 450px;
  height: calc(100vh - 100px);
  min-height: 645px;
  background-color: #f2f2f2; 
  display: block;
  border-radius: 10px;
  flex: 1;
  margin-left: 5px;
}
.dashboard .lower-content .saved .sidebar .displayLocations{
  height:calc(100vh - 200px);
  min-height: 500px;
  overflow-y: auto;
}
.dashboard .sidebar .name{
  color: white;
  width: auto;
  font-size: 30px;
  text-align:left;
  background-color: rgb(40, 40, 135);
  border-top-left-radius: 10px; /* Adjust this value */
  border-top-right-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row; /* Change to row */
  justify-content: space-between; /* Distribute items along the main axis */
  align-items: center;
}
.dashboard .sidebar .name button{
  background-color:  rgb(72, 72, 185);
  padding: 10px;
}
.dashboard .sidebar .name button:hover{
  background-color:  rgb(143, 219, 255);
}

.add-button:hover{
  background-color:  rgb(143, 219, 255);
}

.viewMapButton:hover{
  background-color:  rgb(143, 219, 255);
}

.dashboard .sidebar .locationSettings{
    background-color: rgb(179, 79, 225);
    padding: 10px;
    position: sticky;
}

.checkboxAndDistance {
  display: flex;
  align-items: center;
}
.dashboard .sidebar .locationSettings .setDistance {
  border: none;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 20px;
  font-weight: 400;
  border-radius: 4px;
  height: auto;
  background-color: whitesmoke;
  margin-left: auto;
}
.dashboard .sidebar .locationSettings .setDistance img{
  height: 10px;
}
.dashboard .sidebar .locationSettings .setDistance:hover{
  background-color: rgb(199, 198, 198);
  cursor: pointer;
}
 /* .dashboard .sidebar .locationSettings .dropdown-contentB{
  display: none;
  left: 10%;
  top: 80%;
  position: absolute;
  width: fit-content;
  display: flex;
  flex-direction: column;
}  */
.dashboard .sidebar .locationSettings .dropdown-contentB{
  display: none; /* Initially hide the dropdown content */
  background-color: white;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 10%;
  right: 0%;
  top: 80%;
  position: absolute;
  flex-direction: column;
} 
.dashboard .sidebar .locationSettings .dropdown-contentB span{
  padding: 10px;
}
.dashboard .sidebar .locationSettings .dropdown-contentB span:hover{
  cursor: pointer;
  background-color: #ddd;
}
.dashboard .sidebar-container .sidebar li{
  display: flex;
  padding: 2px 15px 2px 15px;
  font-size: 20px;
  margin-bottom: 15px;
  border-radius: 5px;
  background-color: rgb(225, 225, 245);
  box-shadow: 1px 1px 1px 1px rgba(54, 20, 179, 0.316);
}

.dashboard .sidebar-container .sidebar .location-text {
  flex: 1; /* Take up remaining space */
  font-weight:300;
}
.dashboard .sidebar-container .sidebar .routeDistance {
  flex: 1; /* Take up remaining space */
  font-weight:300;
  font-size: 17px;
}
.dashboard .sidebar-container .sidebar .name-text {
  flex: 1; /* Take up remaining space */
  font-size: 23px;
  font-weight: 540;
}

.dashboard .sidebar-container .sidebar li:hover{
  background-color: rgba(118, 145, 233, 0.518);
  cursor:pointer;
}
.dashboard .lower-content .search-map{
  position: relative;
  flex-direction: column;
  width: 100%;
  height: inherit;
  min-height: 500px;
  padding-right: 5px;
  padding-left: 3px;
}
.dashboard .lower-content .search-map .input-container{
  padding-left: 0px;
  padding-top: 0px;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  min-width: 80%; /*when window maximized, take entire top bar*/
  min-height: 10px;
  margin-bottom: 10px;
}
.dashboard .input-container input{
  font-size: 20px;
  padding-left: 10px;
  min-width: 100px;
  padding: 6px;
  margin-top: 8px;
  margin-right: 8px;
  width: 500px;
  border-radius: 10px;
  border: none;
  flex: 1;
}
.dashboard .input-container .searchButton{
  padding: 10px;
  margin-top: 8px;
  margin-bottom: -4px;
  margin-right: 16px;
  margin-left: -20px;
  height: 42px;
  background: rgb(72, 72, 185);
  color: white;
  font-size: 17px;
  cursor: pointer;
  border: none;
  border-right: 2px solid white;
  z-index: 1;
}
.dashboard .input-container .currentLocationButton{
  margin-top: 8px;
  margin-left: -24px;
  margin-right: 20px;
  padding-top: 5px;
  padding-left: 5px;
  padding: 2px 10px 2px 15px;
  width: auto; 
  height: 38px; 
  display: block; 
  border-radius: 10px;
  cursor: pointer;
  background-color: rgb(72, 72, 185);
}
.dashboard .lower-content .currentLocationButton:hover{
  background-color:  rgb(127, 194, 248);
}

.dashboard .lower-content .searchButton:hover{
  background-color:  rgb(127, 194, 248);
}

.dashboard .lower-content .search-map .map{
  height: calc(100vh - 152px);
  min-height: 585px;
  position: relative;
  width:100%;
  flex-grow: 1;
  flex-shrink: 1;
  border-radius: 10px;
}

@media(max-width: 1220px)
{
  .dashboard .lower-content .search-map .map{
    height: calc(100vh - 166px);
  }
}

ul {
  padding: 7px;
  list-style-type: none;
}

.result-sales-button {
  background-color: purple;
  color: white;
  border: none;
  padding: 0px 16px;
  border-radius: 5px;
  margin-right: -5px;
  margin-top: 5px;
  height: 60px;
  width: 60px;
  cursor: pointer;
  border-radius: 50%;
}

.navigate-button {
  background-color: purple;
  color: white;
  border: none;
  cursor: pointer;
  text-align: center;
  padding: 4px;
  width: 100%;
}
.result-sales-button img{
  height:20px;
  width: auto;
  border-radius: 50%;
}
.add-button {
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  margin-left: 10px;
  margin-bottom: -2px;
  cursor: pointer;
  background-color: rgb(72, 72, 185);
}
.viewMapButton {
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  margin-left: 10px;
  margin-bottom: -2px;
  cursor: pointer;
  background-color: rgb(72, 72, 185);
  display: none;
}
.locationInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: left;
}

.result-sales-button:hover {
  background-color: rgb(123, 215, 252);
}

.navigate-button:hover {
  background-color: rgb(123, 215, 252);
}

.routeDistance {
  margin-right: 10px; /* Adjust spacing between distance and review button */
}
.dashboard .sidebar .name,
.dashboard .sidebar .locationSettings {
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: rgb(40, 40, 135);
}

.dashboard .sidebar .name {
  color: white;
  font-size: 30px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dashboard .sidebar .name button {
  background-color: rgb(72, 72, 185);
  padding: 10px;
}

.dashboard .sidebar .name button:hover {
  background-color: rgb(127, 194, 248);
}

.dashboard .sidebar .locationSettings {
  background-color: rgb(223, 225, 245);
  padding: 10px;
  top: 55px; /* Adjust this value to match the height of the .name element */
}

/* Custom scrollbar track */
::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

/* Custom scrollbar thumb */
::-webkit-scrollbar-thumb {
  background-color: #b5b5b5;
  border-radius: 10px;
}

/* Custom scrollbar corner */
::-webkit-scrollbar-corner {
  background-color: #f1f1f1;
}

/* Optional: Hide scrollbar when not hovering */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/* Show scrollbar on hover */
::-webkit-scrollbar:hover {
  display: block;
}

@media  (max-width: 1100px) {
  .viewMapButton{
    display: block;
  }
  .dashboard .lower-content {
    min-width: 100%;
    max-height: 50%;
    min-height: none;
    height: 100px;
  }
  .dashboard .lower-content .search-map {
    order: 1; 
}

.dashboard .lower-content .saved {
    order: 2; 
}

.dashboard .lower-content .saved{
  min-width: 100%;
  min-height: none;
  max-height: 50%;
  height: 100px;
}

.dashboard .lower-content .search-map .input-container{
  position: fixed;
  top: 80px;
  left: 10px;
  margin-bottom: 20px;
  padding-top: 0px;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  min-height: 10px;
  margin-bottom: 10px;
  max-width: 100%;
}
.dashboard .lower-content .search-map{
  min-width: none;
  min-height: 10px;
  height: 10px;
  max-height: 0px;
}

.dashboard{
  min-height: 20%;
  max-height: 20%;
  height: 10px;
}

.dashboard .lower-content .saved .sidebar-container{
  height: 20px;
  min-height: none;
  margin-left: -5px;
}

.dashboard .lower-content .saved .sidebar{
  top:140px;
  position: fixed;
  width: 100vw;
  max-width: none;
  min-height: none;
  height: 10px;
}

.dashboard .lower-content .saved .sidebar .displayLocations{
  height: 100px;
  min-height: none;
  overflow-y: auto;
}

}